import React from "react";
import Loading from "./Loading";

const url = "https://course-api.com/react-tours-project";

const Tours = () => {
  const [loading, setLoading] = React.useState(true);
  const [tours, setTours] = React.useState([]);
  const [error, setError] = React.useState("");
  const [more, setMore] = React.useState(false);
  const dataTours = async () => {
    setLoading(true);
    try {
      const resp = await fetch(url);
      const tours = await resp.json();
      setLoading(false);
      setTours(tours);
    } catch (error) {
      setLoading(false);
      setError(error.message);
    }
  };
  React.useEffect(() => {
    dataTours();
  }, []);
  const deleteItem = (id) => {
    const newData = tours.filter((tour) => tour.id !== id);
    setTours(newData);
  };

  return loading ? (
    <main>
      <div className="title">
        <Loading />
      </div>
    </main>
  ) : error ? (
    <main>
      <div className="title">
        <h1>{error}</h1>
      </div>
    </main>
  ) : tours.length === 0 ? (
    <main>
      <div className="title">
        <h1>Empty</h1>
        <button onClick={dataTours} className="btn">
          fetch again
        </button>
      </div>
    </main>
  ) : (
    <main>
      <section>
        <div className="title">
          <h2>Tours</h2>
          <div className="underline"></div>
          {tours.map((tour) => {
            return (
              <article className="single-tour" key={tour.id}>
                <img src={tour.image} alt={tour.name} />
                <footer>
                  <div className="tour-info">
                    <h4>{tour.name}</h4>
                    <h4 className="tour-price">${tour.price}</h4>
                  </div>
                  <p>
                    {more ? tour.info : `${tour.info.substring(0, 200)}...`}
                    <button onClick={() => setMore(!more)}>
                      {more ? "Less" : "more"}
                    </button>
                  </p>
                  <button
                    className="delete-btn"
                    onClick={() => deleteItem(tour.id)}
                  >
                    Delete
                  </button>
                </footer>
              </article>
            );
          })}
        </div>
      </section>
    </main>
  );
};
export default Tours;
